import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import unlocakMetaMask from '../plugins/metamask'
import Index from '@/view/index'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children: [
        {
          name: 'Purchase',
          path: 'purchase',
          component: () => import('@/view/Purchase'),
          alias: '/'
        },
        {
          name: 'WhiteList',
          path: 'whitelist',
          component: () => import('@/view/whitelist')
        },
        {
          name: 'Tasks',
          path: 'tasks',
          component: () => import('@/view/tasks')
        },
        {
          name: 'Withdraw',
          path: 'withdraw',
          component: () => import('@/view/Withdraw')
        },
        {
          name: 'Statistics',
          path: 'statistics',
          component: () => import('@/view/Statistics')
        },
        {
          name: 'MasterNode',
          path: 'masternode',
          component: () => import('@/view/MasterNode')
        },
        {
          name: 'News',
          path: 'news',
          component: () => import('@/view/News')
        },
        {
          name: 'NewsDetail',
          path: 'news/newsdetail',
          component: () => import('@/view/NewsDetail')
        },
        {
          name: 'Chat',
          path: 'chat',
          component: () => import('@/view/Chat')
        },
        {
          name: 'NotFound',
          path: '/404',
          component: () => import('@/components/common/NotFound')
        }
      ]
    },
    {// 404
      path: '*',
      redirect: '/404'
    }
  ]
})

// 
router.beforeEach(async (to, from, next) => {
  const status = store.state.metamask.status
  if (status === 0 && Vue.prototype.$metamaskMark === 0) {
    unlocakMetaMask() // todo unlocakMetaMask()
  }

  // todo  metamasktaskswithdrawstatisticsmasternode
  const whiteListLimit = ['Purchase', 'Tasks', 'Withdraw', 'Statistics', 'MasterNode']
  const countDownStatus = store.state.countDown.status // 
  const fromQuery = from.query
  const fromParam = fromQuery.to
  const toParam = to.query.to
  if (fromParam && !toParam) {
    if (whiteListLimit.includes(to.name)) {
      if (countDownStatus) {
        next({
          path: '/whitelist',
          query: fromQuery
        })
      } else { // elseelse next(),
        next({
          path: to.path,
          query: fromQuery
        })
      }
    } else {
      if (to.name === 'WhiteList' && !countDownStatus) { // url
        next({
          path: '/',
          query: fromQuery
        })
      } else {
        next({
          path: to.path,
          query: fromQuery
        })
      }
    }
  } else {
    if (whiteListLimit.includes(to.name)) {
      if (countDownStatus) {
        next({
          path: '/whitelist'
        })
      } else { // elseelse next(),
        next()
      }
    } else {
      if (to.name === 'WhiteList' && !countDownStatus) { // url
        if (!fromParam && toParam) { // purchase
          next({
            path: '/purchase',
            query: to.query
          })
        } else {
          next({
            path: '/purchase'
          })
        }
      } else {
        next()
      }
    }
  }
})

// 
router.afterEach(async (to, from) => {
  // const status = store.state.metamask.status
  // if (status === 0 && Vue.prototype.$metamaskMark === 0) {
  //   await unlocakMetaMask() // todo unlocakMetaMask()
  // }
})

export default router



