// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index'
import Element from 'element-ui'
import i18n from './utils/i18n'
import './element-variables.scss'
import 'element-ui/lib/theme-chalk/display.css'
import ethConf from './lib/eth/config'
import countDown from './plugins/countDown'

countDown() // 
Vue.config.productionTip = false
Vue.prototype.$ethConf = ethConf()
Vue.prototype.$myProvider = null // metamask provider
Vue.prototype.$metamaskMark = 0 //  unlocakMetaMask()
Vue.prototype.$myWeb3 = null // new Web3(provider)
Vue.prototype.$myContracts = null // ,  todo  eth/config plugins/metamask 

Vue.use(Element, {
  i18n: (key, value) => i18n.t(key, value)
})

console.log(':', location.search)

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')



