  // getStraightInviteAddressholdingslevel
  async getUserStatistics ({commit}, userAddressArr) { // userAddressArr 
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        let addressLen = userAddressArr.length
        let begin = 0
        for (let j = 0; j < 100; j++) {
          if (addressLen > 10) {
            const teamData = []
            for (let i = 0; i < 10; i++, begin++) { // 
              addressLen--
              const userAddress = userAddressArr[begin]
              contractRes.methods.getPersonalStatistics().call({
                from: userAddress
              }, function (error, result) {
                if (error) {
                  console.error('getUserStatistics')
                } else {
                  // console.log('getUserStatistics', result)
                  let holdings = ethUtils.weiToEth(result.holdings) // 
                  holdings = ethUtils.numFloatLen(holdings, 4)
                  const level = result.level // 
                  teamData.push({
                    address: userAddress, // todo begin;const,const
                    holdings: holdings,
                    level: level
                  })
                  if (i === 9) {
                    commit('SET_TEAMDATA', teamData)
                  }
                }
              })
            }
          } else {
            const teamData = []
            for (let i = 0; i < addressLen; i++, begin++) {
              const userAddress = userAddressArr[begin]
              contractRes.methods.getPersonalStatistics().call({
                from: userAddress
              }, function (error, result) {
                if (error) {
                  console.error('getUserStatistics')
                } else {
                  // console.log('getUserStatistics', result)
                  let holdings = ethUtils.weiToEth(result.holdings) // 
                  holdings = ethUtils.numFloatLen(holdings, 4)
                  const level = result.level // 
                  teamData.push({
                    address: userAddress, // todo begin
                    holdings: holdings,
                    level: level
                  })
                  if (i === (addressLen - 1)) {
                    commit('SET_TEAMDATA', teamData)
                  }
                }
              })
              if (i === (addressLen - 1)) {
                j = 1000
              }
            }
          }
        }
      }
    }
  },
  //  resonances
  async getContractStatistics ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      const myWeb3 = Vue.prototype.$myWeb3
      let eggPool = await myWeb3.eth.getBalance(Vue.prototype.$ethConf.eggPoolAdress) // todo eth;config
      eggPool = ethUtils.numFloatLen(ethUtils.weiToEth(eggPool), 4)
      data.gasPrice = await myWeb3.eth.getGasPrice()
      data.gasLimit = Vue.prototype.$ethConf.gasLimit
      if (contractRes) {
        data.from = myProvider.selectedAddress
        console.warn('staticscontractStatistics', data)
        contractRes.methods.contractStatistics().call({
          from: data.from,
          gasPrice: data.gasPrice,
          gas: data.gasLimit
        }, function (error, result) {
          if (error) {
            console.error('contractStatistics')
          } else {
            console.log('contractStatistics', result)
            let recommendRankPool = ethUtils.weiToEth(result.recommendRankPool) // 
            recommendRankPool = ethUtils.numFloatLen(recommendRankPool, 4)
            let terminatorPool = ethUtils.weiToEth(result.terminatorPool) // 
            terminatorPool = ethUtils.numFloatLen(terminatorPool, 4)
            let nodePool = ethUtils.weiToEth(result.nodePool) //  todo Key Of Dividend, nodePool
            nodePool = ethUtils.numFloatLen(nodePool, 4)
            commit('SET_CONTRACTSTATISTICS', {recommendRankPool, terminatorPool, eggPool, nodePool})
          }
        })
      }
    }
  },
  // 
  async listRankOfRecommend ({commit}) {
    const data = {}
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      const myWeb3 = Vue.prototype.$myWeb3
      data.gasPrice = await myWeb3.eth.getGasPrice()
      data.gasLimit = Vue.prototype.$ethConf.gasLimit
      if (contractRes) {
        data.from = myProvider.selectedAddress
        console.warn('staticslistRankOfRecommend', data)
        contractRes.methods.listRankOfRecommend().call({
          from: data.from,
          gasPrice: data.gasPrice,
          gas: data.gasLimit
        }, function (error, result) {
          if (error) {
            console.error('listRankOfRecommend', error)
          } else {
            console.log('listRankOfRecommend', result)
            const straightSortAddress = result._straightSort //  [10]
            const inviteNumber = result._inviteNumber //  [10]
            const rankOfRecommend = []
            for (let j = 0; j < inviteNumber.length; j++) {
              if (inviteNumber[j].toNumber() !== 0) {
                const rankObject = {}
                rankObject.straightSortAddress = straightSortAddress[j]
                rankObject.inviteNumber = inviteNumber[j].toNumber()
                rankOfRecommend.push(rankObject)
              }
            }
            commit('SET_RANKOFRECOMMEND', rankOfRecommend)
          }
        })
      }
    }
  },
