  async getLatestTerminatorInfo ({commit}) {
    console.log('')
    const terminatorInfo = []
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractTerm = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractTerm
      if (contractTerm) {
        contractTerm.methods.getLatestTerminatorInfo().call({}, function (error, result) {
          console.log('getLatestTerminatorInfo=======11111111111111111', result)
          if (error) {
            console.error('getLatestTerminatorInfo===========', error)
          } else {
            const blockNum = result.blockNumber
            const addressArray = result.addressArray // [50],0x00000000...
            const amountArray = result.amountArray // [50], 0
            for (let i = 0; i < amountArray.length; i++) {
              if (parseFloat(ethUtils.weiToEth(amountArray[i])) === 0) {
                i = 50
              } else {
                const terminator = {}
                terminator.blockNum = blockNum.toNumber()
                terminator.address = addressArray[i]
                terminator.amount = ethUtils.numFloatLen(ethUtils.weiToEth(amountArray[i]), 4)
                terminatorInfo.push(terminator)
              }
            }
            commit('SET_TERMINATORINFO', terminatorInfo)
          }
        })
      }
    }
  },
